@import 'reset';
@import 'mixin';
@import 'grid';
$mainColor: #50FF44;

body {
    font-size: 16px;
    font-family: "Roboto","Microsoft JhengHei","PingFangTC-Regular";
    background-color: #f2f2f2;
    .wrap {
        margin: 0 auto;
        text-align: center;
        .container {
            max-width: 620px;
            margin: 0 auto;
            padding: 0; }
        header {
            height: 74px;
            background-color: black;
            h1 {
                font-size: 36px;
                color: $mainColor;
                font-style: italic;
                line-height: 74px;
                font-weight: bolder; } }
        .ad {
            padding: 24px 0; }
        main {
            .col {
                padding: 0; }
            .comicBg {
                border: 4px solid black; }
            .comicInfo {
                h2 {
                    font-size: 32px;
                    height: 52px;
                    line-height: 52px;
                    background-color: black;
                    text-align: center;
                    font-weight: bold;
                    color: white; }
                .row {
                    margin: 0;
                    padding: 16px 0px 0px 24px;
                    .title {
                        color: #000000;
                        font-weight: bolder;
                        width: 53px;
                        text-align: left;
                        margin-right: 20px; }
                    i {
                        padding-right: 7px; } }
                .geners {
                    padding-top: 24px; }
                .summary {
                    padding: 24px 0 0 24px;
                    text-align: left;
                    h3 {
                        color: #000000;
                        font-weight: bolder;
                        margin-bottom: 8px; }
                    p {
                        line-height: 24px;
                        &:nth-child(3) {
                            padding-top: 24px; } } } } }
        footer {
            margin-top: 50px;
            .container {
                border: 4px solid black;
                position: relative;
                background-color: white;
                padding: 11px 0; }
            .chapters {
                width: 180px;
                height: 45px;
                line-height: 45px;
                background-color: black;
                color: white;
                position: absolute;
                top: -48;
                left: -4;
                font-size: 24px;
                font-weight: bolder; }
            a {
                display: block;
                padding: 10px 0 10px 24px;
                text-align: left;
                color: black;
                text-decoration: none;
                font-weight: bolder;
                transition: .3s all;
                &:hover {
                    background-color: black;
                    color: white; }
                .newTag {
                    display: inline-block;
                    color: black;
                    background-color: $mainColor;
                    width: 55px;
                    height: 18px;
                    line-height: 18px;
                    text-align: center;
                    margin-left: 16px; } } } } }

